body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f1f1f1;
}
.nav {
  background: rgb(23, 156, 201);
  height: 70px;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-container input {
  height: 43px;
  width: 600px;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  border: none;
  font-size: 16px;
  padding: 0 10px;
  box-sizing: border-box;
}
.search-container {
  position: relative;
}
.search-container input:focus,
.search-container button:focus,
.footer button:focus,
.search-result .movie-info button:focus {
  outline: none;
}
.search-container button:hover,
.footer button:hover,
.search-result .movie-info button:hover {
  cursor: pointer;
}
#search-btn {
  background: #eb7609;
  height: 43px;
  color: #fff;
  border: none;
  font-size: 15px;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  position: relative;
  top: -1px;
}

/* add questions */
#question-card {
  width: 350px;
  border-radius: 25px;

  margin: 150px auto;
}

#question {
  border-radius: 30px;
  background-color: #ebf0fc;
  border-color: #ebf0fc;
  color: #9da3b0;
}
#topic {
  border-radius: 30px;
  background-color: #ebf0fc;
  border-color: #ebf0fc;
  color: #9da3b0;
}

#tags {
  border-radius: 30px;
  background-color: #ebf0fc;
  border-color: #ebf0fc;
  color: #9da3b0;
}

#button {
  border-radius: 30px;
}

#btn {
  position: absolute;
  bottom: -35px;
  padding: 5px;
  margin: 0px 55px;
  align-items: center;
  border-radius: 5px;
}

#container {
  margin-top: 25px;
}

.btn-circle.btn-sm {
  width: 40px;
  height: 40px;
  padding: 2px 0px;
  border-radius: 25px;
  font-size: 14px;
  text-align: center;

  margin: 8px;
}

#add-btn {
  position: absolute;
  width: 9%;
  padding: 5px;
  left: 86%;
  border-radius: 5px;
  outline: none;
}
#home-btn {
  position: absolute;
  width: 8%;
  padding: 5px;
  left: 5%;
  top: 1%;
  border-radius: 5px;
  outline: none;
}
/* question card */
.q-card {
  background: #fff;
  padding: 0;
  border: none;
  border-radius: 3px;
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #f1f1f1;
  display: flex;
}
.q-card:not(:first-child) {
  margin-top: 20px;
}
.q-card:last-child {
  margin-bottom: 50px;
}
.q-card .left {
  flex: 1 1;
  display: flex;
  margin-top: 1%;
  font-weight: bold;
  flex-direction: column;
  justify-content: space-between;
}
.q-card .right {
  position: relative;
  flex: 10;
}
.q-card .topic {
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 600;
  opacity: 0.8;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.q-card .question-description {
  position: relative;
  top: -5%;
  font-size: 1.2rem;
  font-weight: 600;
  left: 21px;
  padding: 0;
}
.footer {
  position: relative;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: darkolivegreen;
  width: 24%;
  padding: 10px 20px;
  box-sizing: border-box;
}

@media only screen and (max-width: 922px) {
  .search-container input {
    height: 43px;
    width: 300px;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    border: none;
    font-size: 16px;
    padding: 0 10px;
    box-sizing: border-box;
  }
}

@media only screen and (max-width: 700px) {
  .search-container input {
    width: 250px;
  }
}

@media only screen and (max-width: 700px) {
  .search-container input {
    width: 200px;
  }
  #add-btn {
    position: absolute;
    width: 12%;
    padding: 5px;
    left: 86%;
    font-size: 0.6rem;
    border-radius: 5px;
    outline: none;
  }

  #home-btn {
    position: absolute;
    width: 12%;
    padding: 5px;
    left: 2%;
    font-size: 0.8rem;
    top: 2%;
    border-radius: 5px;
    outline: none;
  }
}
